import { useState, useEffect } from 'react';
import './Statistics.css';

function Statistics() {
  const [technologies, setTechnologies] = useState([]);

  useEffect(() => {
    const saved = localStorage.getItem('techTrackerData');
    if (saved) {
      try {
        setTechnologies(JSON.parse(saved));
      } catch (error) {
        console.error('  :', error);
      }
    }
  }, []);

  useEffect(() => {
    const handleStorageChange = () => {
      const saved = localStorage.getItem('techTrackerData');
      if (saved) {
        try {
          setTechnologies(JSON.parse(saved));
        } catch (error) {
          console.error('  :', error);
        }
      }
    };

    window.addEventListener('storage', handleStorageChange);

    const interval = setInterval(handleStorageChange, 1000);

    window.addEventListener('technologiesUpdated', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('technologiesUpdated', handleStorageChange);
      clearInterval(interval);
    };
  }, []);

  const calculateProgress = () => {
    if (technologies.length === 0) return 0;
    const completed = technologies.filter(tech => tech.status === 'completed').length;
    return Math.round((completed / technologies.length) * 100);
  };

  const getStatusCounts = () => {
    return {
      total: technologies.length,
      completed: technologies.filter(t => t.status === 'completed').length,
      inProgress: technologies.filter(t => t.status === 'in-progress').length,
      notStarted: technologies.filter(t => t.status === 'not-started').length,
    };
  };

  const progress = calculateProgress();
  const counts = getStatusCounts();

  return (
    <div className="statistics-page">
      <h1>  </h1>

      <div className="stats-grid">
        <div className="stat-card">
          <h3> </h3>
          <div className="progress-circle-container">
            <div className="progress-circle" style={{ '--progress': progress }}>
              <span className="progress-value">{progress}%</span>
            </div>
          </div>
        </div>

        <div className="stat-card">
          <h3> </h3>
          <div className="stat-number">{counts.total}</div>
        </div>

        <div className="stat-card completed">
          <h3></h3>
          <div className="stat-number">{counts.completed}</div>
          <div className="stat-percentage">
            {counts.total > 0 ? Math.round((counts.completed / counts.total) * 100) : 0}%
          </div>
        </div>

        <div className="stat-card in-progress">
          <h3> </h3>
          <div className="stat-number">{counts.inProgress}</div>
          <div className="stat-percentage">
            {counts.total > 0 ? Math.round((counts.inProgress / counts.total) * 100) : 0}%
          </div>
        </div>

        <div className="stat-card not-started">
          <h3> </h3>
          <div className="stat-number">{counts.notStarted}</div>
          <div className="stat-percentage">
            {counts.total > 0 ? Math.round((counts.notStarted / counts.total) * 100) : 0}%
          </div>
        </div>
      </div>

      <div className="progress-chart">
        <h2> </h2>
        <div className="chart-container">
          <div className="chart-bar">
            <div className="chart-label"></div>
            <div className="chart-bar-container">
              <div 
                className="chart-bar-fill completed-fill"
                style={{ width: `${counts.total > 0 ? (counts.completed / counts.total) * 100 : 0}%` }}
              ></div>
              <span className="chart-value">{counts.completed}</span>
            </div>
          </div>

          <div className="chart-bar">
            <div className="chart-label"> </div>
            <div className="chart-bar-container">
              <div 
                className="chart-bar-fill in-progress-fill"
                style={{ width: `${counts.total > 0 ? (counts.inProgress / counts.total) * 100 : 0}%` }}
              ></div>
              <span className="chart-value">{counts.inProgress}</span>
            </div>
          </div>

          <div className="chart-bar">
            <div className="chart-label"> </div>
            <div className="chart-bar-container">
              <div 
                className="chart-bar-fill not-started-fill"
                style={{ width: `${counts.total > 0 ? (counts.notStarted / counts.total) * 100 : 0}%` }}
              ></div>
              <span className="chart-value">{counts.notStarted}</span>
            </div>
          </div>
        </div>
      </div>

      {technologies.length === 0 && (
        <div className="empty-state">
          <p>    .</p>
          <p>    .</p>
        </div>
      )}
    </div>
  );
}

export default Statistics;

