import { useState, useEffect } from 'react';
import './Settings.css';

function Settings() {
  const [settings, setSettings] = useState({
    theme: 'dark',
    autoSave: true,
    notifications: true,
    language: 'ru'
  });

  useEffect(() => {
    const saved = localStorage.getItem('appSettings');
    if (saved) {
      try {
        setSettings(JSON.parse(saved));
      } catch (error) {
        console.error('  :', error);
      }
    }
  }, []);

  const handleClearData = () => {
    if (window.confirm(' ,     ? ')) {
      localStorage.setItem('techTrackerData', JSON.stringify([]));
      alert('  .   .');
      window.location.reload();
    }
  };

  const handleExportData = () => {
    const data = localStorage.getItem('techTrackerData');
    if (data) {
      try {
        const parsed = JSON.parse(data);
        const exportData = Array.isArray(parsed) ? parsed : (parsed.technologies || []);
        const dataStr = JSON.stringify(exportData, null, 2);
        const blob = new Blob([dataStr], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `tech-tracker-backup-${new Date().toISOString().split('T')[0]}.json`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
        alert(' !');
      } catch (error) {
        alert('  .');
        console.error(' :', error);
      }
    } else {
      alert('   .');
    }
  };

  const handleImportData = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (event) => {
        try {
          const parsed = JSON.parse(event.target.result);
          let technologiesToImport;
          
          if (Array.isArray(parsed)) {
            technologiesToImport = parsed;
          } else if (parsed.technologies && Array.isArray(parsed.technologies)) {
            technologiesToImport = parsed.technologies;
          } else {
            alert('  .          technologies.');
            return;
          }
          
          if (technologiesToImport.length === 0) {
            alert('    .');
            return;
          }
          
          localStorage.setItem('techTrackerData', JSON.stringify(technologiesToImport));
          alert('  !   .');
          window.location.reload();
        } catch (error) {
          alert('   .');
          console.error(' :', error);
        }
      };
      reader.readAsText(file);
      e.target.value = '';
    }
  };

  return (
    <div className="settings-page">
      <h1> </h1>
      <div className="settings-section">
        <h2> </h2>
        <div className="data-actions">
          <button onClick={handleExportData} className="action-btn export-btn">
              
          </button>
          <label className="action-btn import-btn">
              
            <input
              type="file"
              accept=".json"
              onChange={handleImportData}
              style={{ display: 'none' }}
            />
          </label>
          <button onClick={handleClearData} className="action-btn danger-btn">
               
          </button>
        </div>
        
      </div>

      
    </div>
  );
}

export default Settings;

