import React from 'react';
import './QuickActions.css';


function QuickActions({ onMarkAllCompleted, onResetAll, onRandomNext, technologies }) {
  const handleExport = () => {
    const data = {
      exportedAt: new Date().toISOString(),
      technologies: technologies
    };

    const dataStr = JSON.stringify(data, null, 2);

    console.log('  :', dataStr);

    const blob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `tech-tracker-export-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="quick-actions">
      <h3> </h3>
      <div className="actions-buttons">
        <button
          className="action-btn complete-all"
          onClick={onMarkAllCompleted}
        >
              
        </button>

        <button
          className="action-btn reset-all"
          onClick={onResetAll}
        >
             
        </button>

        <button
          className="action-btn random-next"
          onClick={onRandomNext}
        >
              
        </button>

        <button
          className="action-btn export-data"
          onClick={handleExport}
        >
            
        </button>
      </div>

    
    </div>
  );
}

export default QuickActions;
