import React, { useState, useEffect, useCallback } from 'react';
import './TechnologyCard.css';
import Modal from './Modal';
import DeadlineForm from './DeadlineForm';
import useTechnologyResources from '../hooks/useTechnologyResources';

function TechnologyCard({ id, title, description, status, notes, deadline, onStatusChange, onNotesChange, onDelete, onSetDeadline }) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isDeadlineModalOpen, setIsDeadlineModalOpen] = useState(false);
  const [localNotes, setLocalNotes] = useState(notes || '');
  const [resources, setResources] = useState([]);
  const [resourcesLoaded, setResourcesLoaded] = useState(false);
  const { loading: resourcesLoading, fetchResources } = useTechnologyResources();

  const handleCardClick = () => {
    setIsModalOpen(true);
  };

  const handleDeleteClick = (e) => {
    e.stopPropagation();
    onDelete();
  };

  const handleStatusClick = (e) => {
    e.stopPropagation();
    onStatusChange();
  };

  const handleSaveNotes = () => {
    onNotesChange(localNotes);
    setIsModalOpen(false);
  };

  const handleCloseModal = () => {
    setLocalNotes(notes || '');
    setIsModalOpen(false);
  };

  const loadResources = useCallback(async () => {
    try {
      const loadedResources = await fetchResources(title);
      setResources(loadedResources);
      setResourcesLoaded(true);
    } catch (err) {
      console.error('  :', err);
    }
  }, [title, fetchResources]);

  useEffect(() => {
    if (isModalOpen && !resourcesLoaded) {
      loadResources();
    }
  }, [isModalOpen, resourcesLoaded, loadResources]);

  return (
    <>
      <div
        className={`technology-card ${status}`}
        onClick={handleCardClick}
        style={{ cursor: 'pointer' }}
      >
        <div className="card-header">
          <h3>{title}</h3>
          <span className="status-icon">
            {status === 'completed' ? '  ' : status === 'in-progress' ? '   ' : '   '}
          </span>
        </div>

        <p className="card-description">{description}</p>
        
        {deadline && (
          <div className="card-deadline" style={{ 
            marginTop: '12px', 
            padding: '8px 12px', 
            background: 'linear-gradient(90deg, rgba(16, 185, 129, 0.15) 0%, rgba(59, 130, 246, 0.1) 100%)', 
            borderRadius: '12px',
            fontSize: '14px',
            color: 'rgba(255, 255, 255, 0.9)',
            borderLeft: '3px solid rgba(16, 185, 129, 0.5)'
          }}>
             : {new Date(deadline).toLocaleDateString('ru-RU', {
              year: 'numeric',
              month: 'long',
              day: 'numeric'
            })}
          </div>
        )}
        
        <div className="card-actions">
          <button
            className="status-btn"
            onClick={handleStatusClick}
          >
             
          </button>
          <button
            className="delete-btn"
            onClick={handleDeleteClick}
          >
            
          </button>
        </div>
      </div>

      <Modal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        title={title}
      >
        <div>
          <p className="modal-description">{description}</p>
          <div className="deadline-section">
            <div className="deadline-info">
              <h4> </h4>
              {deadline ? (
                <div className="deadline-display">
                  <p> <strong>{new Date(deadline).toLocaleDateString('ru-RU', {
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric'
                  })}</strong></p>
                  {new Date(deadline) < new Date() && (
                    <p className="deadline-overdue" style={{ color: 'rgba(255, 107, 107, 1)' }}>
                        
                    </p>
                  )}
                </div>
              ) : (
                <p></p>
              )}
            </div>
            <button
              type="button"
              onClick={(e) => {
                e.stopPropagation();
                setIsDeadlineModalOpen(true);
              }}
              className="set-deadline-btn"
              style={{
                padding: '8px 16px',
                marginTop: '12px',
                background: 'rgba(33, 165, 185, 0.3)',
                color: 'white',
                border: '2px solid transparent',
                borderRadius: '12px',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '500',
                boxShadow: '0 4px 15px rgba(33, 165, 185, 0.3)',
                transition: 'all 0.3s ease'
              }}
              onMouseEnter={(e) => {
                e.target.style.background = 'rgba(33, 165, 185, 0.6)';
                e.target.style.boxShadow = '0 8px 25px rgba(33, 165, 185, 0.5), 0 0 20px rgba(33, 165, 185, 0.3)';
              }}
              onMouseLeave={(e) => {
                e.target.style.background = 'rgba(33, 165, 185, 0.3)';
                e.target.style.boxShadow = '0 4px 15px rgba(33, 165, 185, 0.3)';
              }}
            >
              {deadline ? ' ' : ' '}
            </button>
          </div>

          <div className="notes-section">
            <label htmlFor="notes"> :</label>
            <textarea
              id="notes"
              value={localNotes}
              onChange={(e) => setLocalNotes(e.target.value)}
              placeholder=""
              rows="6"
            />
            <div className="notes-hint">
              {localNotes.length > 0 ? `  (${localNotes.length} )` : ' '}
            </div>
          </div>

          <div className="resources-section">
            <div className="resources-header">
              <h4> </h4>
              {!resourcesLoaded && (
                <button 
                  onClick={loadResources} 
                  disabled={resourcesLoading}
                  className="load-resources-btn"
                >
                  {resourcesLoading ? '...' : ' '}
                </button>
              )}
            </div>
            
            {resourcesLoading && (
              <div className="resources-loading">
                <span>  ...</span>
              </div>
            )}

            {resourcesLoaded && resources.length > 0 && (
              <div className="resources-list">
                {resources.map((resource, index) => (
                  <div key={index} className="resource-item">
                    <a 
                      href={resource.url} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="resource-link"
                    >
                      <span className="resource-title">{resource.title}</span>
                      {resource.stars && (
                        <span className="resource-stars"> {resource.stars}</span>
                      )}
                    </a>
                    <p className="resource-description">{resource.description}</p>
                    <span className="resource-type">{resource.type}</span>
                  </div>
                ))}
              </div>
            )}

            {resourcesLoaded && resources.length === 0 && (
              <p className="no-resources">  </p>
            )}
          </div>
          <div className="modal-buttons">
            <button onClick={handleSaveNotes}>
               
            </button>
            <button onClick={handleCloseModal} className="cancel-btn">
              
            </button>
          </div>
        </div>
      </Modal>

      <Modal
        isOpen={isDeadlineModalOpen}
        onClose={() => setIsDeadlineModalOpen(false)}
        title="  "
      >
        <DeadlineForm
          technology={{ id, title, description, deadline }}
          onSave={(newDeadline) => {
            if (onSetDeadline) {
              onSetDeadline(newDeadline);
            }
            setIsDeadlineModalOpen(false);
          }}
          onCancel={() => setIsDeadlineModalOpen(false)}
        />
      </Modal>
    </>
  );
}

export default TechnologyCard;