import { useState, useCallback } from 'react';

function useTechnologyResources() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchResources = useCallback(async (technologyName) => {
    try {
      setLoading(true);
      setError(null);

      const response = await fetch(
        `https://api.github.com/search/repositories?q=${encodeURIComponent(technologyName)}&sort=stars&order=desc&per_page=5`,
        {
          headers: {
            'Accept': 'application/vnd.github.v3+json'
          }
        }
      );

      if (!response.ok) {
        throw new Error(` HTTP: ${response.status}`);
      }

      const data = await response.json();
      
      const resources = data.items.map(item => ({
        title: item.name,
        description: item.description || ' ',
        url: item.html_url,
        stars: item.stargazers_count,
        type: 'repository'
      }));

      return resources;

    } catch (err) {
      console.warn('     API,  mock :', err.message);
      
      const mockResources = [
        {
          title: `${technologyName} -  `,
          description: `   ${technologyName}`,
          url: `https://example.com/${technologyName.toLowerCase()}/docs`,
          type: 'documentation'
        },
        {
          title: `${technologyName} -  `,
          description: `    ${technologyName}`,
          url: `https://example.com/${technologyName.toLowerCase()}/tutorial`,
          type: 'tutorial'
        },
        {
          title: `${technologyName} -  `,
          description: `   ${technologyName}  `,
          url: `https://example.com/${technologyName.toLowerCase()}/course`,
          type: 'video'
        }
      ];

      return mockResources;
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    loading,
    error,
    fetchResources
  };
}

export default useTechnologyResources;

