import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../App.css';
import './Login.css';

const users = [
  { username: 'damirxxl_XXl@mail.ru', password: 'password', name: '' },
  { username: 'admin', password: 'admin123', name: '' },
  { username: 'user', password: 'user123', name: '' },
];

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    
    const user = users.find(
      u => u.username === username && u.password === password
    );
    
    if (user) {
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('username', user.name || username);
      localStorage.setItem('userEmail', username);
      
      onLogin(user.name || username);
      
      navigate('/');
    } else {
      alert('   ');
    }
  };

  return (
    <div className="login-page">
      <div className="login-container">
        <h1>  </h1>
        <form onSubmit={handleSubmit} className="login-form">
          <div className="form-group">
            <label htmlFor="username"> :</label>
            <input
              type="text"
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="  "
              required
              className="form-input"
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="password">:</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=" "
              required
              className="form-input"
            />
          </div>
          
          <button type="submit" className="login-btn">
            
          </button>
          
          <div className="login-hint">
            <p> :</p>
            <ul style={{ textAlign: 'left', marginTop: '10px', fontSize: '14px' }}>
              <li>damirxxl_XXl@mail.ru / password</li>
              <li>admin / admin123</li>
              <li>user / user123</li>
            </ul>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Login;
