import { useState } from 'react';
import './BulkStatusEditor.css';

function BulkStatusEditor({ technologies, onBulkStatusChange, onClose }) {
  const [selectedTechIds, setSelectedTechIds] = useState([]);
  const [targetStatus, setTargetStatus] = useState('in-progress');
  const [selectAll, setSelectAll] = useState(false);

  const handleTechToggle = (techId) => {
    setSelectedTechIds(prev => {
      if (prev.includes(techId)) {
        return prev.filter(id => id !== techId);
      } else {
        return [...prev, techId];
      }
    });
  };

  const handleSelectAll = () => {
    if (selectAll) {
      setSelectedTechIds([]);
    } else {
      setSelectedTechIds(technologies.map(tech => tech.id));
    }
    setSelectAll(!selectAll);
  };

  const handleApplyStatus = () => {
    if (selectedTechIds.length === 0) {
      alert('    ');
      return;
    }

    const confirmMessage = `  ${selectedTechIds.length} ${selectedTechIds.length === 1 ? '' : ''}  "${getStatusLabel(targetStatus)}"?`;
    
    if (window.confirm(confirmMessage)) {
      onBulkStatusChange(selectedTechIds, targetStatus);
      setSelectedTechIds([]);
      setSelectAll(false);
      if (onClose) {
        onClose();
      }
    }
  };

  const getStatusLabel = (status) => {
    const labels = {
      'not-started': ' ',
      'in-progress': ' ',
      'completed': ''
    };
    return labels[status] || status;
  };

  const getStatusCount = (status) => {
    return technologies.filter(tech => tech.status === status).length;
  };

  const [statusFilter, setStatusFilter] = useState('all');
  const filteredTechnologies = statusFilter === 'all' 
    ? technologies 
    : technologies.filter(tech => tech.status === statusFilter);

  return (
    <div className="bulk-status-editor">
      <div className="bulk-editor-header">
        <h3>  </h3>
        {onClose && (
          <button 
            onClick={onClose} 
            className="close-btn"
            aria-label=" "
          >
            
          </button>
        )}
      </div>

      <div className="bulk-editor-stats" role="status" aria-live="polite">
        <div className="stat-item">
          <span> :</span>
          <strong>{technologies.length}</strong>
        </div>
        <div className="stat-item">
          <span>:</span>
          <strong>{selectedTechIds.length}</strong>
        </div>
        <div className="stat-item">
          <span> :</span>
          <strong>{getStatusCount('not-started')}</strong>
        </div>
        <div className="stat-item">
          <span> :</span>
          <strong>{getStatusCount('in-progress')}</strong>
        </div>
        <div className="stat-item">
          <span>:</span>
          <strong>{getStatusCount('completed')}</strong>
        </div>
      </div>

      <div className="bulk-editor-controls">
        <div className="control-group">
          <label htmlFor="status-filter">  :</label>
          <select
            id="status-filter"
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            aria-label="   "
          >
            <option value="all"></option>
            <option value="not-started"> </option>
            <option value="in-progress"> </option>
            <option value="completed"></option>
          </select>
        </div>

        <div className="control-group">
          <label htmlFor="target-status"> :</label>
          <select
            id="target-status"
            value={targetStatus}
            onChange={(e) => setTargetStatus(e.target.value)}
            aria-label="     "
          >
            <option value="not-started"> </option>
            <option value="in-progress"> </option>
            <option value="completed"></option>
          </select>
        </div>
      </div>

      <div className="bulk-editor-actions">
        <button
          type="button"
          onClick={handleSelectAll}
          className="btn-select-all"
          aria-label={selectAll ? '   ' : ' '}
        >
          {selectAll ? '    ' : '  '}
        </button>
        
        <button
          type="button"
          onClick={handleApplyStatus}
          disabled={selectedTechIds.length === 0}
          className="btn-apply"
          aria-describedby={selectedTechIds.length === 0 ? 'apply-help' : undefined}
        >
            "{getStatusLabel(targetStatus)}"  {selectedTechIds.length} {selectedTechIds.length === 1 ? '' : ''}
        </button>
        
        {selectedTechIds.length === 0 && (
          <span id="apply-help" className="help-text">
                
          </span>
        )}
      </div>

      <div className="bulk-editor-list" role="list">
        {filteredTechnologies.length === 0 ? (
          <p className="no-techs">  </p>
        ) : (
          filteredTechnologies.map(tech => {
            const isSelected = selectedTechIds.includes(tech.id);
            return (
              <div
                key={tech.id}
                className={`tech-select-item ${isSelected ? 'selected' : ''} ${tech.status}`}
                role="listitem"
              >
                <label className="tech-checkbox-label">
                  <input
                    type="checkbox"
                    checked={isSelected}
                    onChange={() => handleTechToggle(tech.id)}
                    aria-label={`  ${tech.title}`}
                  />
                  <span className="tech-title">{tech.title}</span>
                  <span className={`tech-status-badge ${tech.status}`}>
                    {getStatusLabel(tech.status)}
                  </span>
                </label>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
}

export default BulkStatusEditor;

