import { useState, useEffect } from 'react';
import './DeadlineForm.css';

function DeadlineForm({ technology, onSave, onCancel }) {
  const [deadline, setDeadline] = useState(technology?.deadline || '');
  const [errors, setErrors] = useState({});
  const [isFormValid, setIsFormValid] = useState(false);

  useEffect(() => {
    const newErrors = {};
    
    if (deadline) {
      const deadlineDate = new Date(deadline);
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      
      if (deadlineDate < today) {
        newErrors.deadline = '  ?';
      }
      
      const maxDate = new Date();
      maxDate.setFullYear(maxDate.getFullYear() + 8);
      if (deadlineDate > maxDate) {
        newErrors.deadline = '        ';
      }
    }

    setErrors(newErrors);
    setIsFormValid(Object.keys(newErrors).length === 0);
  }, [deadline]);

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (isFormValid) {
      onSave(deadline);
    }
  };

  const handleCancel = () => {
    setDeadline(technology?.deadline || '');
    setErrors({});
    if (onCancel) {
      onCancel();
    }
  };

  const formatDateDDMMYYYY = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const year = date.getFullYear();
    return `${day}.${month}.${year}`;
  };

  return (
    <form onSubmit={handleSubmit} className="deadline-form-modal" noValidate>
      {technology && (
        <div className="deadline-form-tech-info">
          <p><strong>:</strong> {technology.title}</p>
          {technology.deadline && (
            <p><strong> :</strong> {formatDateDDMMYYYY(technology.deadline)}</p>
          )}
        </div>
      )}

      <div className="form-group">
        <label htmlFor="deadline" className="required">
            
        </label>
        <input
          id="deadline"
          name="deadline"
          type="date"
          value={deadline}
          onChange={(e) => setDeadline(e.target.value)}
          className={errors.deadline ? 'error' : ''}
          aria-required="true"
          aria-invalid={!!errors.deadline}
          aria-describedby={errors.deadline ? 'deadline-error' : 'deadline-help'}
          min={new Date().toISOString().split('T')[0]}
        />
        {errors.deadline && (
          <span 
            id="deadline-error" 
            className="error-message" 
            role="alert"
            aria-live="polite"
          >
            {errors.deadline}
          </span>
        )}
        <div id="deadline-help" className="help-text">
          ..<br />
           ,      
        </div>
      </div>

      {deadline && !errors.deadline && (
        <div className="deadline-preview" role="status">
          <p>  : <strong>{formatDateDDMMYYYY(deadline)}</strong></p>
        </div>
      )}

      {!isFormValid && deadline && (
        <div className="form-validation-info" role="alert">
              
        </div>
      )}

      <div className="form-actions">
        <button
          type="submit"
          disabled={!isFormValid && deadline}
          className="btn-primary"
          aria-describedby={!isFormValid && deadline ? 'validation-info' : undefined}
        >
           
        </button>
        
        <button
          type="button"
          onClick={handleCancel}
          className="btn-secondary"
        >
          
        </button>

        {deadline && (
          <button
            type="button"
            onClick={() => {
              setDeadline('');
              onSave('');
            }}
            className="btn-danger"
            aria-label=" "
          >
             
          </button>
        )}
      </div>
    </form>
  );
}

export default DeadlineForm;

