import React, { createContext, useState, useContext, useEffect, useMemo } from 'react';
import { ThemeProvider, createTheme, CssBaseline } from '@mui/material';
import { NotificationProvider } from './NotificationContext';

const ThemeContext = createContext();

export const useThemeMode = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useThemeMode must be used within ThemeModeProvider');
  }
  return context;
};

const getTheme = (isDark) => createTheme({
  palette: {
    mode: isDark ? 'dark' : 'light',
    primary: {
      main: '#3b82f6',
      light: '#60a5fa',
      dark: '#2563eb',
    },
    secondary: {
      main: '#8b5cf6',
      light: '#a78bfa',
      dark: '#7c3aed',
    },
    background: {
      default: isDark ? '#0f172a' : '#f0f9ff',
      paper: isDark ? '#1e293b' : '#ffffff',
    },
  },
  typography: {
    fontFamily: '"Inter", "Roboto", "Helvetica", "Arial", sans-serif',
    h4: {
      fontWeight: 600,
    },
    h6: {
      fontWeight: 600,
    },
  },
  shape: {
    borderRadius: 8,
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          boxShadow: isDark ? '0 2px 8px rgba(0,0,0,0.3)' : '0 2px 8px rgba(0,0,0,0.1)',
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          fontWeight: 600,
        },
      },
    },
  },
});

export const ThemeModeProvider = ({ children }) => {
  const [isDarkMode, setIsDarkMode] = useState(() => {
    const saved = localStorage.getItem('themeMode');
    return saved === 'dark';
  });

  const updateCSSVariables = (isDark) => {
    const root = document.documentElement;
    if (isDark) {
      root.style.setProperty('--text-color', 'rgb(255, 255, 255)');
      root.style.setProperty('--box-color', 'rgba(30, 41, 59, 0.8)');
      root.style.setProperty('--back-color', 'rgb(15, 23, 42)');
      root.style.setProperty('--navbar-color', 'rgba(30, 41, 59, 0.9)');
      root.style.setProperty('--accent-border', 'rgba(59, 130, 246, 0.4)');
      root.style.setProperty('--accent-border-hover', 'rgba(59, 130, 246, 0.7)');
      root.style.setProperty('--accent-border-focus', 'rgba(139, 92, 246, 0.8)');
      root.style.setProperty('--accent-shadow', 'rgba(59, 130, 246, 0.3)');
      root.style.setProperty('--accent-shadow-hover', 'rgba(139, 92, 246, 0.5)');
      root.style.setProperty('--accent-bg', 'rgba(59, 130, 246, 0.15)');
      root.style.setProperty('--accent-text', 'rgb(59, 130, 246)');
      root.style.setProperty('--accent-secondary', 'rgb(139, 92, 246)');
      root.style.setProperty('--success-color', 'rgb(16, 185, 129)');
      root.style.setProperty('--warning-color', 'rgb(251, 191, 36)');
      root.style.setProperty('--error-color', 'rgb(239, 68, 68)');
    } else {
      root.style.setProperty('--text-color', 'rgb(15, 23, 42)');
      root.style.setProperty('--box-color', 'rgba(255, 255, 255, 0.95)');
      root.style.setProperty('--back-color', 'rgb(240, 249, 255)');
      root.style.setProperty('--navbar-color', 'rgba(255, 255, 255, 0.9)');
      root.style.setProperty('--accent-border', 'rgba(59, 130, 246, 0.3)');
      root.style.setProperty('--accent-border-hover', 'rgba(59, 130, 246, 0.6)');
      root.style.setProperty('--accent-border-focus', 'rgba(139, 92, 246, 0.7)');
      root.style.setProperty('--accent-shadow', 'rgba(59, 130, 246, 0.2)');
      root.style.setProperty('--accent-shadow-hover', 'rgba(139, 92, 246, 0.4)');
      root.style.setProperty('--accent-bg', 'rgba(59, 130, 246, 0.1)');
      root.style.setProperty('--accent-text', 'rgb(59, 130, 246)');
      root.style.setProperty('--accent-secondary', 'rgb(139, 92, 246)');
      root.style.setProperty('--success-color', 'rgb(16, 185, 129)');
      root.style.setProperty('--warning-color', 'rgb(251, 191, 36)');
      root.style.setProperty('--error-color', 'rgb(239, 68, 68)');
    }
  };

  useEffect(() => {
    updateCSSVariables(isDarkMode);
  }, []);

  useEffect(() => {
    localStorage.setItem('themeMode', isDarkMode ? 'dark' : 'light');
    updateCSSVariables(isDarkMode);
  }, [isDarkMode]);

  const toggleTheme = () => {
    setIsDarkMode(prev => !prev);
  };

  const theme = useMemo(() => getTheme(isDarkMode), [isDarkMode]);

  return (
    <ThemeContext.Provider value={{ isDarkMode, toggleTheme }}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <NotificationProvider>
          {children}
        </NotificationProvider>
      </ThemeProvider>
    </ThemeContext.Provider>
  );
};

