import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Navigation from './components/Navigation';
import Home from './pages/Home';
import Statistics from './pages/Statistics';
import Settings from './pages/Settings';
import Login from './pages/Login';
import ProtectedRoute from './components/ProtectedRoute';
import { ThemeModeProvider } from './contexts/ThemeContext';
import { useNotification } from './contexts/NotificationContext';

const initialTechnologies = [
  {
    id: 1,
    title: 'React Components',
    description: '     React',
    status: 'completed',
    notes: ''
  },
  {
    id: 2,
    title: 'JSX Syntax',
    description: '   JSX',
    status: 'in-progress',
    notes: ''
  },
  {
    id: 3,
    title: 'State Management component',
    description: '    (useState, useEffect)',
    status: 'not-started',
    notes: ''
  }
];

function AppContent() {
  const { showNotification } = useNotification();
  
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [username, setUsername] = useState('');

  useEffect(() => {
    const loggedIn = localStorage.getItem('isLoggedIn') === 'true';
    const user = localStorage.getItem('username') || '';
    const userEmail = localStorage.getItem('userEmail') || '';
    setIsLoggedIn(loggedIn);
    setUsername(user);
    if (userEmail) {
      setCurrentUserEmail(userEmail);
    }
  }, []);

  const [currentUserEmail, setCurrentUserEmail] = useState(() => {
    return localStorage.getItem('userEmail') || '';
  });

  const getUserDataKey = (email) => {
    return email ? `techTrackerData_${email}` : 'techTrackerData';
  };

  const handleLogin = (user) => {
    setIsLoggedIn(true);
    setUsername(user);
    const userEmail = localStorage.getItem('userEmail') || '';
    if (userEmail) {
      setCurrentUserEmail(userEmail);
      const dataKey = getUserDataKey(userEmail);
      const saved = localStorage.getItem(dataKey);
      if (saved !== null) {
        try {
          const parsed = JSON.parse(saved);
          if (Array.isArray(parsed)) {
            setTechnologies(parsed);
          } else {
            setTechnologies(initialTechnologies);
          }
        } catch (error) {
          console.error('   :', error);
          setTechnologies(initialTechnologies);
        }
      } else {
        setTechnologies(initialTechnologies);
      }
    }
    showNotification(` , ${user}!`, 'success');
  };

  const handleLogout = () => {
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('username');
    localStorage.removeItem('userEmail');
    setIsLoggedIn(false);
    setUsername('');
    setCurrentUserEmail('');
    showNotification('   ', 'info');
  };

  const [technologies, setTechnologies] = useState(() => {
    try {
      const userEmail = localStorage.getItem('userEmail') || '';
      const dataKey = getUserDataKey(userEmail);
      const saved = localStorage.getItem(dataKey);
      if (saved !== null) {
        const parsed = JSON.parse(saved);
        if (Array.isArray(parsed)) {
          return parsed;
        } else {
          console.warn('  localStorage   ,   ');
          return initialTechnologies;
        }
      }
    } catch (error) {
      console.error('    localStorage:', error);
    }
    return initialTechnologies;
  });

  useEffect(() => {
    try {
      const dataKey = getUserDataKey(currentUserEmail);
      localStorage.setItem(dataKey, JSON.stringify(technologies));
      console.log('   localStorage  :', currentUserEmail || '');
      window.dispatchEvent(new Event('technologiesUpdated'));
    } catch (error) {
      console.error('    localStorage:', error);
    }
  }, [technologies, currentUserEmail]);

  const handleStatusChange = (id) => {
    setTechnologies(prevTechs => prevTechs.map(tech => {
      if (tech.id === id) {
        let nextStatus;
        if (tech.status === 'not-started') nextStatus = 'in-progress';
        else if (tech.status === 'in-progress') nextStatus = 'completed';
        else nextStatus = 'not-started';
        
        const statusMessages = {
          'in-progress': '    " "',
          'completed': '  !',
          'not-started': '   " "'
        };
        showNotification(statusMessages[nextStatus], nextStatus === 'completed' ? 'success' : 'info');
        
        return { ...tech, status: nextStatus };
      }
      return tech;
    }));
  };

  const handleMarkAllCompleted = () => {
    setTechnologies(prevTechs =>
      prevTechs.map(tech => ({ ...tech, status: 'completed' }))
    );
    showNotification('    !', 'success');
  };

  const handleResetAll = () => {
    setTechnologies(prevTechs =>
      prevTechs.map(tech => ({ ...tech, status: 'not-started' }))
    );
    showNotification('   ', 'info');
  };

  const handleRandomNext = () => {
    const notStarted = technologies.filter(
      tech => tech.status === 'not-started'
    );

    if (notStarted.length === 0){
      showNotification('   !', 'warning');
      return;
    }
    const randomTech = notStarted[Math.floor(Math.random() * notStarted.length)];
    setTechnologies(prevTechs =>prevTechs.map(tech => {
      if (tech.id === randomTech.id){
        showNotification(` : ${randomTech.title}`, 'info');
        return { ...tech, status :'in-progress'};
      }
      return tech;
    }));
  };

  const handleDelete = (id) => {
    const tech = technologies.find(t => t.id === id);
    const del = window.confirm(' ?');
    if (del){
      setTechnologies(prevTechs => prevTechs.filter(tech => tech.id !== id));
      showNotification(` "${tech?.title || ''}" `, 'info');
    }
  };

  const handleNotesChange = (id, newNotes) => {
    setTechnologies(prevTechs =>
      prevTechs.map(tech =>
        tech.id === id ? { ...tech, notes: newNotes } : tech
      )
    );
  };

  const handleAddTechnology = (newTech) => {
    setTechnologies(prevTechs => [...prevTechs, newTech]);
    showNotification(` "${newTech.title}" `, 'success');
  };

  const handleDeadlineChange = (id, deadline) => {
    const tech = technologies.find(t => t.id === id);
    setTechnologies(prevTechs =>
      prevTechs.map(tech =>
        tech.id === id ? { ...tech, deadline: deadline } : tech
      )
    );
    if (deadline) {
      showNotification(`  "${tech?.title || ''}" `, 'success');
    } else {
      showNotification(`  "${tech?.title || ''}" `, 'info');
    }
  };

  const handleBulkStatusChange = (techIds, newStatus) => {
    const statusLabels = {
      'not-started': ' ',
      'in-progress': ' ',
      'completed': ''
    };
    setTechnologies(prevTechs =>
      prevTechs.map(tech =>
        techIds.includes(tech.id) ? { ...tech, status: newStatus } : tech
      )
    );
    showNotification(
      `   "${statusLabels[newStatus]}"  ${techIds.length} ${techIds.length === 1 ? '' : ''}`,
      newStatus === 'completed' ? 'success' : 'info'
    );
  };

  return (
    <Router>
      <div className="App">
        <h1></h1>
        <Navigation 
          isLoggedIn={isLoggedIn} 
          username={username} 
          onLogout={handleLogout} 
        />
        
        <Routes>
          <Route 
            path="/" 
            element={
              <Home 
                technologies={technologies}
                onStatusChange={handleStatusChange}
                onNotesChange={handleNotesChange}
                onDelete={handleDelete}
                onMarkAllCompleted={handleMarkAllCompleted}
                onResetAll={handleResetAll}
                onRandomNext={handleRandomNext}
                onAddTechnology={handleAddTechnology}
                onDeadlineChange={handleDeadlineChange}
                onBulkStatusChange={handleBulkStatusChange}
              />
            } 
          />
          <Route path="/statistics" element={<Statistics />} />
          <Route 
            path="/login" 
            element={<Login onLogin={handleLogin} />} 
          />
          <Route 
            path="/settings" 
            element={
              <ProtectedRoute isLoggedIn={isLoggedIn}>
                <Settings />
              </ProtectedRoute>
            } 
          />
        </Routes>
      </div>
    </Router>
  );
}

function App() {
  return (
    <ThemeModeProvider>
      <AppContent />
    </ThemeModeProvider>
  );
}

export default App;
