import { useState } from 'react';
import '../App.css';
import TechnologyCard from '../components/TechnologyCard';
import ProgressHeader from '../components/ProgressHeader';
import QuickActions from '../components/QuickActions';
import TechnologySearch from '../components/TechnologySearch';
import BulkStatusEditor from '../components/BulkStatusEditor';

function Home({ technologies, onStatusChange, onNotesChange, onDelete, onMarkAllCompleted, onResetAll, onRandomNext, onAddTechnology, onDeadlineChange, onBulkStatusChange }) {
  const safeTechnologies = Array.isArray(technologies) ? technologies : [];
  
  const [activeFilter, setActiveFilter] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [newTech, setNewTech] = useState({
    title: '',
    description: '',
  });
  const [showBulkEditor, setShowBulkEditor] = useState(false);

  const handleSearchChange = (query) => {
    setSearchQuery(query);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewTech(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleAddTechnology = (e, onAddTechnology) => {
    e.preventDefault();
    if (!newTech.title.trim() || !newTech.description.trim()) {
      alert('   ');
      return;
    }
    const maxId = safeTechnologies.length > 0 
      ? Math.max(...safeTechnologies.map(t => t.id), 0) 
      : 0;
    const newTechnology = {
      id: maxId + 1,
      title: newTech.title.trim(),
      description: newTech.description.trim(),
      status: 'not-started',
      notes: ''
    };
    if (onAddTechnology) {
      onAddTechnology(newTechnology);
    }
    setNewTech({ title: '', description: '' });
  };

  const getFilteredTechnologies = () => {
    let filtered = safeTechnologies;

    if (activeFilter !== 'all') {
      filtered = filtered.filter(tech => tech.status === activeFilter);
    }

    if (searchQuery.trim()) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter(tech =>
        tech.title.toLowerCase().includes(query) ||
        tech.description.toLowerCase().includes(query)
      );
    }
    
    return filtered;
  };

  const filteredTechnologies = getFilteredTechnologies();

  const handleDeadlineSave = (tech, deadline) => {
    if (onDeadlineChange) {
      onDeadlineChange(tech.id, deadline);
    }
  };

  return (
    <div className="home-page">
      <ProgressHeader items={safeTechnologies} />

      <QuickActions
        onMarkAllCompleted={onMarkAllCompleted}
        onResetAll={onResetAll}
        onRandomNext={onRandomNext}
        technologies={safeTechnologies}
      />

      <div style={{ margin: '20px auto', maxWidth: '800px', textAlign: 'center' }}>
        <button
          onClick={() => setShowBulkEditor(!showBulkEditor)}
          className="filter-btn"
          style={{ padding: '12px 24px', fontSize: '16px' }}
        >
          {showBulkEditor ? '  ' : '   '}
        </button>
      </div>

      {showBulkEditor && (
        <BulkStatusEditor
          technologies={safeTechnologies}
          onBulkStatusChange={onBulkStatusChange}
          onClose={() => setShowBulkEditor(false)}
        />
      )}


      <div className="add-tech-form">
        <h3>  </h3>
        <form onSubmit={(e) => handleAddTechnology(e, onAddTechnology)}>
          <div className="form-group">
            <label htmlFor="title" className="form-label">:</label>
            <input
              type='text'
              id='title'
              name='title'
              value={newTech.title}
              onChange={handleInputChange}
              placeholder='    '
              required
              className="form-input"
            />
          </div>
          <div className="form-group">
            <label htmlFor="description" className="form-label">:</label>
            <textarea
              id='description'
              name='description'
              value={newTech.description}
              onChange={handleInputChange}
              placeholder='  '
              rows='4'
              required
              className="form-textarea"
            />
          </div>
          <button className='add-tech-btn' type="submit">
             
          </button>
        </form>
      </div>

      <TechnologySearch 
        technologies={safeTechnologies}
        onSearchChange={handleSearchChange}
      />

      <div className="filters">
        <h3>  :</h3>
        <div className="filter-buttons">
          <button
            className={`filter-btn ${activeFilter === 'all' ? 'active' : ''}`}
            onClick={() => setActiveFilter('all')}
          >
             ({safeTechnologies.length})
          </button>
          <button
            className={`filter-btn ${activeFilter === 'not-started' ? 'active' : ''}`}
            onClick={() => setActiveFilter('not-started')}
          >
              ({safeTechnologies.filter(t => t.status === 'not-started').length})
          </button>
          <button
            className={`filter-btn ${activeFilter === 'in-progress' ? 'active' : ''}`}
            onClick={() => setActiveFilter('in-progress')}
          >
              ({safeTechnologies.filter(t => t.status === 'in-progress').length})
          </button>
          <button
            className={`filter-btn ${activeFilter === 'completed' ? 'active' : ''}`}
            onClick={() => setActiveFilter('completed')}
          >
             ({safeTechnologies.filter(t => t.status === 'completed').length})
          </button>
        </div>
      </div>

      <div className="tech-list">
        {filteredTechnologies.map((tech) => (
          <TechnologyCard
            key={tech.id}
            id={tech.id}
            title={tech.title}
            description={tech.description}
            status={tech.status}
            notes={tech.notes}
            deadline={tech.deadline}
            onStatusChange={() => onStatusChange(tech.id)}
            onNotesChange={(newNotes) => onNotesChange(tech.id, newNotes)}
            onDelete={() => onDelete(tech.id)}
            onSetDeadline={(deadline) => handleDeadlineSave(tech, deadline)}
          />
        ))}
      </div>
    </div>
  );
}

export default Home;

